* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Manrope", sans-serif;
  scrollbar-width:thin;
  scrollbar-color: #6969dd #e0e0e0;
}


@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  h1 {
    font-weight: 700;
    font-size: 56px;
    text-transform: uppercase;
    line-height: 58px;
    letter-spacing: 2px;
  }

  h2 {
    font-weight: 700;
    font-size: 40px;
    text-transform: uppercase;
    line-height: 44px;
    letter-spacing: 1.5px;
  }

  h3 {
    font-weight: 700;
    font-size: 32px;
    text-transform: uppercase;
    line-height: 36px;
    letter-spacing: 1.15px;
  }

  h4 {
    font-weight: 700;
    font-size: 28px;
    text-transform: uppercase;
    line-height: 38px;
    letter-spacing: 2px;
  }

  h5 {
    font-weight: 700;
    font-size: 24px;
    text-transform: uppercase;
    line-height: 33px;
    letter-spacing: 1.7px;
  }

  h6 {
    font-weight: 700;
    font-size: 18px;
    text-transform: uppercase;
    line-height: 24px;
    letter-spacing: 1.3px;
  }

  p {
    font-weight: 500;
    font-size: 15px;
    line-height: 25px;
  }
}

@layer components {
  .subtitle {
    font-family: "Manrope", sans-serif;
    font-weight: 700;
    font-size: 13px;
    text-transform: uppercase;
    line-height: 25px;
    letter-spacing: 1px;
    color: theme("colors.orange_primary");
  }

  .title_orange_spaced {
    font-size: 14px;
    text-transform: uppercase;
    line-height: 19px;
    letter-spacing: 10px;
    color: theme("colors.orange_primary");
  }

  .button_orange {
    width: 160px;
    height: 48px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: theme("colors.orange_primary");
    color: white;
    text-transform: uppercase;
    font-size: 13px;
    letter-spacing: 1px;
    font-weight: 500;
    border-radius: 5px;
    
  }

  .button_orange:hover {
    background-color: theme("colors.orange_secondary");
  }

  .button_orange:active{
    background-color: theme("colors.orange_primary");
  }

  .button_black {
    width: 160px;
    height: 48px;
    background-color: theme("colors.off_black");
    border: none;
    text-transform: uppercase;
    font-size: 13px;
    letter-spacing: 1px;
    font-weight: 700;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    transition:.3s;
    border-radius: 5px;
  }

  .button_black:hover {
    background-color: white;
    color: theme("colors.off_black");
    border: 1px solid theme("colors.off_black");
    filter: drop-shadow(-2px 1px 9px #1f1f1f77);
  }

  .button_transparent {
    width: 160px;
    height: 48px;
    background-color: transparent;
    border: 1px solid theme("colors.off_black");
    text-transform: uppercase;
    font-size: 13px;
    letter-spacing: 1px;
    font-weight: 700;
    color: theme("colors.off_black");
    transition: .3s;
    border-radius: 5px;
  }

  .button_transparent:hover {
    background-color: theme("colors.off_black");
    color: white;
    border: 1px solid theme("colors.off_black");
    filter: drop-shadow(-2px 1px 9px #8f8f8f);
  }

  .button_shop {
    display: flex;
    align-items: center;
    gap: 8px;
    text-transform: uppercase;
    color: #808080;
    letter-spacing: 1px;
    font-weight: 700;
  }

  .button_shop:hover {
    color: theme("colors.orange_primary");
  }

  .nav_item{
    font-size: 13px;
    letter-spacing: 2px;
    line-height: 25px;
    text-transform: uppercase;
    color: white;
    transition: .5s;
  }

  .nav_item:hover{
    color: theme("colors.orange_primary");
    cursor: pointer;
  }

  .form_input_box{
    border: 1px solid rgb(197, 197, 197);
    border-radius: 10px;
    padding: .75rem ;
  }

  .form_input_box:focus{
    border: 2px solid theme("colors.orange_primary");
    outline: none;
  }

   .form_input_error{
    border: 1px solid rgb(211, 0, 0);
    background-color:rgba(255, 152, 152, 0.185);
    border-radius: 10px;
    padding: .75rem ;
  }

  .checkboxdiv:has(.form_input_box:checked) {
    border: 2px solid theme("colors.orange_primary");
    accent-color: theme("colors.orange_primary");
  }


}
